<template>
    <div>
        <h1 class="tittle">{{articleShow.title}}</h1>
        <!---->
        <div class="time">
        </div>
        <article v-html="article" class="markdown-body"></article>
        <el-backtop target=".page"></el-backtop>
    </div>
</template>

<script>
    import {getArticle} from "../../../service/api";
    import marked from "marked";

    export default {
        name: "Articles",
        props: ["id"],
        data() {
            return {
                articleShow: []
            };
        },
        computed: {
            article() {
                let article = this.articleShow.article;
                try {
                    return marked(article);
                }catch (e) {
                }

            }
        },
        created() {
            let params = {
                id: this.id
            };
            //
            getArticle(params)
                .then(response => {
                    this.articleShow = response.data;
                })
                .catch(error => {
                    console.log(error);
                });
        }
    };

</script>

<style lang="scss" scoped>
    @import "../../../style/statement";
    @import "../../../style/markdown.css";

    .tittle {
        font-size: 30px;
        margin: 30px 0;
        padding: 10px 0;
    }

    .time {
        line-height: 60px;
        margin: 30px 0;
    }

    @include styleAllPhone {
        .tittle {
            font-size: 25px;
        }
    }

</style>
